"use client";
import { useState, useRef, useEffect } from "react";
import { ChevronLeft, ChevronRight } from "lucide-react";

// Deterministic pseudo-random function
function seededRandom(seed) {
  let x = Math.sin(seed) * 10000;
  return x - Math.floor(x);
}

// Round numbers for hydration safety
function format(num) {
  return Number(num.toFixed(2));
}

const CardStackCollection = () => {
  const [scrollProgress, setScrollProgress] = useState(0);
  const [hoveredCard, setHoveredCard] = useState(null);
  const [activeCarouselIndex, setActiveCarouselIndex] = useState(0);
  const scrollContainerRef = useRef(null);

  const collections = [
    { id: 1, title: "Poster 1", src: "/api/placeholder/200/304", alt: "Poster 1" },
    { id: 2, title: "Poster 2", src: "/api/placeholder/200/304", alt: "Poster 2" },
    { id: 3, title: "Poster 3", src: "/api/placeholder/200/304", alt: "Poster 3" },
    { id: 4, title: "Poster 4", src: "/api/placeholder/200/304", alt: "Poster 4" },
    { id: 5, title: "Poster 5", src: "/api/placeholder/200/304", alt: "Poster 5" },
    { id: 6, title: "Poster 6", src: "/api/placeholder/200/304", alt: "Poster 6" },
  ];

  // Generate deterministic messy offsets
  const cardOffsets = collections.map((c, index) => {
    const seed = c.id * 100 + index;
    return {
      offsetX: format((seededRandom(seed) - 0.5) * 20),
      offsetY: format((seededRandom(seed + 1) - 0.5) * 10),
      rotation: format((seededRandom(seed + 2) - 0.5) * 15),
    };
  });

  // Track scroll progress
  useEffect(() => {
    const handleScroll = () => {
      if (!scrollContainerRef.current) return;
      const { scrollTop, clientHeight, scrollHeight } =
        scrollContainerRef.current;
      const progress = scrollTop / (scrollHeight - clientHeight);
      setScrollProgress(progress);
    };

    const container = scrollContainerRef.current;
    if (container) {
      container.addEventListener("scroll", handleScroll);
      handleScroll();
    }

    return () => {
      if (container) container.removeEventListener("scroll", handleScroll);
    };
  }, []);

  // Carousel navigation
  const nextSlide = () => {
    setActiveCarouselIndex((prev) => (prev + 1) % collections.length);
  };

  const prevSlide = () => {
    setActiveCarouselIndex((prev) => (prev - 1 + collections.length) % collections.length);
  };

  const goToSlide = (index) => {
    setActiveCarouselIndex(index);
  };

  // Card style based on scroll (3 sections)
  const getCardStyle = (index, total) => {
    const baseZIndex = total - index;
    let t = scrollProgress * 3; // 3 stages

    if (t < 1) {
      // Section 1: messy stack
      const { offsetX, offsetY, rotation } = cardOffsets[index];
      return {
        transform: `translateX(${format(
          offsetX * (1 - t) + (index - (total - 1) / 2) * 200 * t
        )}px) translateY(${format(offsetY * (1 - t))}px) rotate(${format(
          rotation * (1 - t) + (index - (total - 1) / 2) * 3 * t
        )}deg)`,
        zIndex: baseZIndex,
        transition: "transform 0.5s ease-out",
        opacity: 1,
      };
    } else if (t < 2) {
      // Section 2: spread with rotation
      const spreadT = t - 1;
      const spreadDistance = 200;
      const angle = (index - (total - 1) / 2) * 3 * (1 - spreadT);
      return {
        transform: `translateX(${(index - (total - 1) / 2) * spreadDistance}px) translateY(0px) rotate(${angle}deg)`,
        zIndex: hoveredCard === index ? baseZIndex + 1000 : baseZIndex + 500,
        transition: "transform 0.5s ease-out",
        opacity: 1,
      };
    } else {
      // Section 3: carousel
      const carouselT = t - 2;
      const isActive = index === activeCarouselIndex;
      const diff = index - activeCarouselIndex;
      
      // Calculate position relative to active slide
      let translateX = diff * 250; // Distance between slides
      let scale = 1;
      let opacity = 1;
      let zIndex = baseZIndex;
      
      if (isActive) {
        scale = 1.1;
        zIndex = baseZIndex + 1000;
      } else if (Math.abs(diff) === 1) {
        scale = 0.9;
        opacity = 0.7;
        zIndex = baseZIndex + 500;
      } else if (Math.abs(diff) === 2) {
        scale = 0.8;
        opacity = 0.4;
        zIndex = baseZIndex + 200;
      } else {
        scale = 0.7;
        opacity = 0.2;
        zIndex = baseZIndex + 100;
      }
      
      // Smooth transition from spread to carousel
      const mixT = Math.min(carouselT * 2, 1); // Faster transition into carousel
      const spreadX = (index - (total - 1) / 2) * 220;
      const finalX = translateX;
      const finalScale = 1 + (scale - 1) * mixT;
      const finalOpacity = 1 + (opacity - 1) * mixT;
      
      return {
        transform: `translateX(${format(spreadX * (1 - mixT) + finalX * mixT)}px) translateY(0px) rotate(0deg) scale(${format(finalScale)})`,
        zIndex: zIndex,
        transition: "transform 0.6s ease-out, opacity 0.6s ease-out",
        opacity: finalOpacity,
      };
    }
  };

  const handleCardHover = (index) => setHoveredCard(index);
  const handleCardLeave = () => setHoveredCard(null);

  const handleCardClick = (index) => {
    if (scrollProgress * 3 >= 2) { // Only in carousel section
      setActiveCarouselIndex(index);
    }
  };

  const scrollToTop = () => {
    if (scrollContainerRef.current) {
      scrollContainerRef.current.scrollTo({ top: 0, behavior: "smooth" });
    }
  };

  const scrollToSection2 = () => {
    if (scrollContainerRef.current) {
      const section2Top = window.innerHeight;
      scrollContainerRef.current.scrollTo({ top: section2Top, behavior: "smooth" });
    }
  };

  const isCarouselActive = scrollProgress * 3 >= 2;

  return (
    <div className="relative h-screen">
      {/* Scrollable sections */}
      <div
        ref={scrollContainerRef}
        className="h-full w-full overflow-y-scroll snap-y snap-mandatory"
      >
        {/* Section 1 */}
        <section className="h-screen w-full bg-gradient-to-br from-blue-50 to-indigo-100 snap-start flex items-center justify-center">
          <div className="text-center">
            <h1 className="text-6xl font-bold text-gray-800 mb-4">Welcome</h1>
            <p className="text-xl text-gray-600">Scroll to see the magic happen</p>
          </div>
        </section>

        {/* Section 2 */}
        <section className="h-screen w-full snap-start relative bg-gradient-to-br from-purple-50 to-pink-100">
          <div className="absolute top-20 left-1/2 transform -translate-x-1/2 text-center z-10 pointer-events-none">
            <h1 className="text-4xl font-bold text-gray-800 mb-2">The Ultimate</h1>
            <h2 className="text-5xl font-black text-gray-900 mb-6">COLLECTIONS</h2>
            <div className="flex justify-center gap-4 mb-6">
              <span className="px-3 py-1.5 bg-cyan-400 text-white rounded-full text-xs font-medium">
                for everyone
              </span>
              <span className="px-3 py-1.5 bg-orange-400 text-white rounded-full text-xs font-medium">
                new styles
              </span>
            </div>
          </div>

          <div className="absolute bottom-20 left-1/2 transform -translate-x-1/2 text-center z-10 pointer-events-none">
            <p className="text-gray-600 text-sm max-w-md mx-auto mb-6">
              We Go First Choice Our Collections 12 Sessions We Create New Models Every Week. And We In A Few Items.
            </p>
            <button
              onClick={scrollToTop}
              className="px-6 py-2.5 bg-black text-white rounded-full font-medium hover:bg-gray-800 transition-colors duration-300 shadow-lg hover:shadow-xl pointer-events-auto"
            >
              Back to Top
            </button>
          </div>
        </section>

        {/* Section 3 */}
        <section className="h-screen w-full snap-start bg-gradient-to-br from-green-50 to-teal-100 relative">
          <div className="absolute top-20 left-1/2 transform -translate-x-1/2 text-center z-10 pointer-events-none">
            <h1 className="text-4xl font-bold text-gray-800 mb-2">Best Selling Product Of The</h1>
            <h2 className="text-5xl font-black text-gray-900 mb-6">COLLECTIONS</h2>
            <div className="flex justify-center gap-4 mb-6">
              <span className="px-3 py-1.5 bg-cyan-400 text-white rounded-full text-xs font-medium">
                for everyone
              </span>
              <span className="px-3 py-1.5 bg-orange-400 text-white rounded-full text-xs font-medium">
                new styles
              </span>
            </div>
          </div>

          <div className="absolute bottom-20 left-1/2 transform -translate-x-1/2 text-center z-10 pointer-events-none">
            <p className="text-gray-600 text-sm max-w-md mx-auto mb-6">
              We Go First Choice Our Collections 12 Sessions We Create New Models Every Week. And We In A Few Items.
            </p>
            <button
              onClick={scrollToSection2}
              className="px-6 py-2.5 bg-black text-white rounded-full font-medium hover:bg-gray-800 transition-colors duration-300 shadow-lg hover:shadow-xl pointer-events-auto"
            >
              Back to Section 2
            </button>
          </div>
        </section>
      </div>

      {/* Fixed Card Stack */}
      <div className="fixed inset-0 flex flex-col items-center justify-center pointer-events-none z-[999]">
        <div className="relative flex-1 flex items-center justify-center">
          <div className="relative transition-all duration-1000 w-full">
            <div className="relative h-80 flex items-center justify-center overflow-hidden">
              {collections.map((collection, index) => (
                <div
                  key={collection.id}
                  className={`absolute w-[200px] h-[304px] rounded-lg shadow-xl cursor-pointer overflow-hidden pointer-events-auto ${
                    hoveredCard === index ? "shadow-2xl" : ""
                  } ${isCarouselActive && index === activeCarouselIndex ? "shadow-2xl" : ""}`}
                  style={getCardStyle(index, collections.length)}
                  onMouseEnter={() => handleCardHover(index)}
                  onMouseLeave={handleCardLeave}
                  onClick={() => handleCardClick(index)}
                >
                  <div className="relative w-full h-full">
                    <div 
                      className="w-full h-full bg-gradient-to-br from-gray-200 to-gray-400 rounded-2xl flex items-center justify-center text-gray-600"
                    >
                      <div className="text-center">
                        <div className="text-4xl mb-2">ðŸ“·</div>
                        <div className="text-sm font-medium">{collection.title}</div>
                      </div>
                    </div>
                    {isCarouselActive && index === activeCarouselIndex && (
                      <div className="absolute inset-0 border-2 border-blue-400 rounded-2xl pointer-events-none"></div>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* Carousel Controls - Only visible in section 3 */}
      {isCarouselActive && (
        <div className="fixed bottom-8 left-1/2 transform -translate-x-1/2 z-[1000] pointer-events-auto">
          <div className="flex items-center gap-4 bg-white/90 backdrop-blur-sm rounded-full px-6 py-3 shadow-lg">
            <button
              onClick={prevSlide}
              className="p-2 rounded-full bg-gray-100 hover:bg-gray-200 transition-colors"
            >
              <ChevronLeft size={20} />
            </button>
            
            <div className="flex gap-2">
              {collections.map((_, index) => (
                <button
                  key={index}
                  onClick={() => goToSlide(index)}
                  className={`w-3 h-3 rounded-full transition-colors ${
                    index === activeCarouselIndex ? "bg-blue-500" : "bg-gray-300"
                  }`}
                />
              ))}
            </div>
            
            <button
              onClick={nextSlide}
              className="p-2 rounded-full bg-gray-100 hover:bg-gray-200 transition-colors"
            >
              <ChevronRight size={20} />
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default CardStackCollection;